import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Row, Col, Container } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { getIconForHardSkill } from '../../mock/aboutMe/hardSkillsData';
import '../../style/pages/_hardSkills.scss';

// Function to generate a unique key
const generateUniqueKey = () => {
  return Math.random().toString(36).substring(7);
};

const HardSkills = ({ hardSkills }) => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 600);
    };

    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <Container fluid className="hardskill-page-container">
      <Row className={`hardskill-row ${isMobile ? 'mobile-layout' : ''}`}>
        {Object.keys(hardSkills).map((category) => (
          <Col lg={4} sm={12} key={category} className="hardskill-column">
            <div className="hardskill-card">
              <h2 className="hardskill-category-heading">{category}</h2>
              {hardSkills[category].map((hardskill) => (
                <div className="hardskill-item" key={generateUniqueKey()}>
                  <FontAwesomeIcon
                    icon={getIconForHardSkill(hardskill)}
                    className="hardskill-icon"
                  />
                  <span className="hardskill-text">{hardskill}</span>
                </div>
              ))}
            </div>
          </Col>
        ))}
      </Row>
    </Container>
  );
};

HardSkills.propTypes = {
  hardSkills: PropTypes.objectOf(PropTypes.arrayOf(PropTypes.string)).isRequired,
};

export default HardSkills;
